import { createStore } from "vuex";
import createPersistedState from "vuex-persistedstate";

const store = createStore({
  state: {
    leftDrawerOpen: false,
    selectedTopMenu: null,
    topMenuItems: [
      {
        name: "소개",
        path: "/index",
        children: [
          { name: "용어 정의", path: "/index/sub1" },
          { name: "인증 프로세스", path: "/index/sub2" },
          { name: "자가진단 도구", path: "/index/sub3" },
        ],
      },
      {
        name: "인식의 용이성",
        path: "/guide1",
        children: [
          { name: "대체 텍스트", path: "/guide1/sub1" },
          { name: "멀티미디어 대체 수단", path: "/guide1/sub2" },
          { name: "적응성", path: "/guide1/sub3" },
          { name: "명료성", path: "/guide1/sub4" },
        ],
      },
      {
        name: "운용의 용이성",
        path: "/guide2",
        children: [
          { name: "입력 장치 접근성", path: "/guide2/sub1" },
          { name: "충분한 시간 제공", path: "/guide2/sub2" },
          { name: "광 과민성 발작 예방", path: "/guide2/sub3" },
          { name: "쉬운 내비게이션", path: "/guide2/sub4" },
          { name: "입력 방식", path: "/guide2/sub5" },
        ],
      },
      {
        name: "이해의 용이성",
        path: "/guide3",
        children: [
          { name: "가독성", path: "/guide3/sub1" },
          { name: "예측 가능성", path: "/guide3/sub2" },
          { name: "입력 도움", path: "/guide3/sub3" },
        ],
      },
      {
        name: "견고성",
        path: "/guide4",
        children: [
          { name: "문법 준수", path: "/guide4/sub1" },
          { name: "웹 애플리케이션 접근성", path: "/guide4/sub2" },
        ],
      },
    ],
  },
  mutations: {
    toggleLeftDrawer(state) {
      state.leftDrawerOpen = !state.leftDrawerOpen;
    },
    setSelectedTopMenu(state, menuItem) {
      state.selectedTopMenu = menuItem;
    },
  },
  getters: {
    getSelectedTopMenu(state) {
      return state.selectedTopMenu || state.topMenuItems[0];
    },
  },
  actions: {
    initializeTopMenu({ commit, state }) {
      if (!state.selectedTopMenu) {
        commit("setSelectedTopMenu", state.topMenuItems[0]);
      }
    },
  },
  plugins: [
    createPersistedState({
      paths: ["selectedTopMenu"],
    }),
  ],
});

store.dispatch("initializeTopMenu");

export default store;
