<template>
  <q-layout view="hHh lpR lFf" class="inner-wrap">
    <q-header bordered class="text-white" height-hint="98">
      <q-toolbar>
        <q-btn dense flat round icon="menu" @click="toggleLeftDrawer" />

        <q-toolbar-title
          ><router-link to="/" class="row items-center" @click="initializeTopMenu"
            >웹접근성 가이드
            <q-badge color="primary" class="q-ml-xs">v1.0</q-badge></router-link
          ></q-toolbar-title
        >
      </q-toolbar>

      <q-tabs align="left">
        <q-route-tab
          v-for="(item, index) in topMenuItems"
          :key="index"
          :to="item.path"
          :label="item.name"
          @click="selectTopMenu(item)"
        />
      </q-tabs>
    </q-header>

    <SideMenu v-model="store.state.leftDrawerOpen" />

    <q-page-container>
      <router-view />
    </q-page-container>
  </q-layout>
</template>

<script setup>
import { useStore } from "vuex";
import { computed } from "vue";
import SideMenu from "@/components/SideMenu.vue";

const store = useStore();
const topMenuItems = computed(() => store.state.topMenuItems);

const toggleLeftDrawer = () => {
  store.commit("toggleLeftDrawer");
};

const selectTopMenu = (item) => {
  store.commit("setSelectedTopMenu", item);
};

const initializeTopMenu = () => {
  store.commit("setSelectedTopMenu", store.state.topMenuItems[0]);
};
</script>

<style lang="scss">
@import "./assets/css/style";
</style>
