import { createRouter, createWebHistory } from "vue-router";
import Index from "@/views/index.vue";
import Guide1 from "@/views/guide1.vue";
import Guide2 from "@/views/guide2.vue";
import Guide3 from "@/views/guide3.vue";
import Guide4 from "@/views/guide4.vue";

const routes = [
  {
    path: "/",
    redirect: "/index/sub1",
  },
  {
    path: "/index",
    redirect: "/index/sub1",
    component: Index,
    children: [
      { path: "sub1", component: () => import("@/views/indexSub1-1.vue") },
      { path: "sub2", component: () => import("@/views/indexSub1-2.vue") },
      { path: "sub3", component: () => import("@/views/indexSub1-3.vue") },
    ],
  },
  {
    path: "/guide1",
    redirect: "/guide1/sub1",
    component: Guide1,
    children: [
      { path: "sub1", component: () => import("@/views/guideSub1-1.vue") },
      { path: "sub2", component: () => import("@/views/guideSub1-2.vue") },
      { path: "sub3", component: () => import("@/views/guideSub1-3.vue") },
      { path: "sub4", component: () => import("@/views/guideSub1-4.vue") },
    ],
  },
  {
    path: "/guide2",
    redirect: "/guide2/sub1",
    component: Guide2,
    children: [
      { path: "sub1", component: () => import("@/views/guideSub2-1.vue") },
      { path: "sub2", component: () => import("@/views/guideSub2-2.vue") },
      { path: "sub3", component: () => import("@/views/guideSub2-3.vue") },
      { path: "sub4", component: () => import("@/views/guideSub2-4.vue") },
      { path: "sub5", component: () => import("@/views/guideSub2-5.vue") },
    ],
  },
  {
    path: "/guide3",
    redirect: "/guide3/sub1",
    component: Guide3,
    children: [
      { path: "sub1", component: () => import("@/views/guideSub3-1.vue") },
      { path: "sub2", component: () => import("@/views/guideSub3-2.vue") },
      { path: "sub3", component: () => import("@/views/guideSub3-3.vue") },
    ],
  },
  {
    path: "/guide4",
    redirect: "/guide4/sub1",
    component: Guide4,
    children: [
      { path: "sub1", component: () => import("@/views/guideSub4-1.vue") },
      { path: "sub2", component: () => import("@/views/guideSub4-2.vue") },
    ],
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
